import React from "react";
import {LifeStamp} from "../lib/stamps.utils";

export default function ListTimeStamps(props: any) {
    const {list} = props;
    return (
        <div className="stamps">
            {list && list.length > 0 && list.map(({note, ts}: LifeStamp, i: number) => {
                return (<div className="stamp" key={`list-item-${i}`}>
                    <div className="ts">
                        {ts}
                    </div>
                    <div className="note">
                        {note}
                    </div>
                </div>);
            })}
        </div>
    );
}